/* Base Styles */
.courses-container {
  background-color: #F3F4F8;
  padding: 3rem 0;
  width: 100%;
}

.courses-wrapper {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.courses-header {
  margin-bottom: 3rem;
}

.courses-title {
  font-size: 1.75rem;
  font-weight: 500;
  color: #000;
  line-height: 1.3;
  margin-bottom: 0.5rem;
}

.courses-subtitle {
  font-size: 0.75rem;
  font-weight: 400;
  color: #4B5563;
  display: block;
}

/* Courses Grid */
.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1.25rem;
}

/* Course Card */
.course-card {
  position: relative;
  overflow: hidden;
  border-radius: 0.5rem;
  background: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  min-height: 400px; /* Increased for consistent footer positioning */
}

.course-card-content {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.course-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}

.course-image-container {
  position: relative;
  overflow: hidden;
  height: 9rem;
  width: 100%;
}

.course-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.course-card:hover .course-image {
  transform: scale(1.05);
}

.course-categories {
  position: absolute;
  top: 0.75rem;
  left: 0.75rem;
  display: flex;
  gap: 0.5rem;
}

.category-badge {
  font-size: 0.7rem;
  font-weight: 400;
  padding: 0.25rem 0.75rem;
  color: white;
  border-radius: 0.25rem;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.category-primary {
  background-color: #1D4ED8;
}

.category-secondary {
  background-color: #BE185D;
}

.course-details {
  padding: 1rem;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.course-meta {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.75rem;
}

.meta-item {
  display: flex;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 400;
  color: #4B5563;
}

.meta-icon {
  margin-right: 0.5rem;
}

.star-icon {
  color: #F59E0B;
}

.course-title {
  font-size: 0.95rem;
  font-weight: 400;
  color: #000;
  margin: 0.75rem 0;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
}

.instructor-info {
  display: flex;
  align-items: center;
  margin-top: 0.75rem;
}

.instructor-image {
  width: 2.25rem;
  height: 2.25rem;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 0.75rem;
}

.instructor-name {
  font-size: 0.75rem;
  font-weight: 400;
  color: #4B5563;
}

.course-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-top: 1px solid #E5E7EB;
  margin-top: auto;
  width: 100%;
}

.course-price {
  font-size: 1rem;
  font-weight: 500;
  color: #2563EB;
}

.course-link {
  display: flex;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 400;
  color: #2563EB;
  transition: color 0.3s ease;
}

.course-link:hover {
  color: #1E40AF;
}

.link-arrow {
  margin-left: 0.5rem;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .courses-container {
    padding: 2rem 0;
  }

  .courses-wrapper {
    width: 95%;
  }

  .courses-header {
    margin-bottom: 2rem;
  }

  .courses-title {
    font-size: 1.5rem;
  }

  .courses-subtitle {
    font-size: 0.7rem;
  }

  .courses-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;
  }

  .course-image-container {
    height: 8rem;
  }

  .course-card {
    min-height: 380px;
  }
}

@media (max-width: 480px) {
  .courses-container {
    padding: 1.5rem 0;
  }

  .courses-title {
    font-size: 1.25rem;
  }

  .courses-subtitle {
    font-size: 0.65rem;
  }

  .courses-grid {
    grid-template-columns: 1fr;
  }

  .course-card {
    min-height: 360px;
    max-width: 100%;
  }

  .course-image-container {
    height: 7.5rem;
  }
}